body {
  background-color: #FFF; /* For browsers that do not support gradients */
  background-image: linear-gradient(#F6D8FF, #AFEFFF);
}
a:link, a:visited {
  color: #fff;
  text-decoration: none;
  transition: color 0.5s ease;
}
a:hover {
  color: #393b4c;
}

.App {
  text-align: center;
  font-family: "Roboto", sans-serif;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin-top: -50px;
  margin-bottom: 24px;
}

.fb-page-btn, .fb-group-btn {
  width: 250px;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  margin: 8px 0;
  font-weight: 300;
  letter-spacing: 0.3px;
  background-color: #A996EC;
  display: inline-block;
  cursor: pointer;
}
